export function createDisabledOption(label) {
  return {
    id: 'no-option',
    type: 'disabled',
    label,
    noAddon: true,
    'aria-disabled': true,
    className: 'no-options',
    tabIndex: -1,
  };
}

export function noAddonOptions(options) {
  return options.map(op => ({ ...op, noAddon: true }));
}
